












































import common from '../../common/common.js';
import {
	mapState
} from 'vuex';

export default {
	data() {
		return {
			imageList: [],
			//imageListStr:""

		}
	},
	onUnload() {
		this.imageList = [],
		this.imageListStr=""
	},
	computed: {
		...mapState(['user']),
	},
	methods: {

		chooseImage: async function() {
				let imgcount = 3;
				let that =this;
				if (this.imageList.length == 3) {
					let isContinue = await this.isFullImg();
					if (!isContinue) {
						return false;
					}
				}
				
				uni.chooseImage({
					count: imgcount - this.imageList.length,
					success: (res) => {
						this.imageList = this.imageList.concat(res.tempFilePaths);
						console.log(JSON.stringify(res))
						res.tempFilePaths.forEach(function(value,key){
							that.uploadImage(value);
						})
						
						
					}
				})
			},
			delImage: function(e) {
				this.imageList.splice(e, 1);
			},
			isFullImg: function() {
				return new Promise((res) => {
					uni.showModal({
						content: "最多只能选择3张图片,是否清空现有图片？",
						success: (e) => {
							if (e.confirm) {
								this.imageList = [];
								res(true);
							} else {
								res(false)
							}
						},
						fail: () => {
							res(false)
						}
					})
				})
			},
			uploadImage: function(e) {

				let that = this;
				console.log(e);

				let imgurl = [{
					"uri": e,
					"name":"file[]"
				}];
				uni.uploadFile({
					url: common.hosturl + "Upload/multupload.html",
					files: imgurl,
					formData: {
						token: this.user.user.token
					},
					success: (uploadFileRes) => {
						console.log(JSON.stringify(uploadFileRes));
					},
				})
			},
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			}
	}
}



